
import { defineComponent } from "vue";
import McvIcon from "@/components/Icon.vue";

export default defineComponent({
  name: "McvFavorites",
  props: {
    isFavorited: {
      type: Boolean,
      required: true,
    },
    count: {
      type: Number,
      required: true,
    },
    isUpdatingFavoriteStatus: {
      type: Boolean,
      required: true,
    },
  },
  emit: ["clickFavorite"],
  methods: {
    favoriteStatusChanged(): void {
      this.$emit("clickFavorite");
    },
  },
  components: {
    McvIcon,
  },
});
